.header{
    color: whitesmoke;
    display: flex;
    padding: 20px;
    align-items: center;
    justify-content: space-between;
    overflow: scroll;
}

.header::-webkit-scrollbar{
    display: none;
}

.header_icons{
    display: flex;
    align-items: center;
    text-align: center;
    cursor: pointer;
  
}

.header_icon{
    display: flex;
    flex-direction: column;
    width: 100px;
     
}

.header_icon > p {
    display: none;
    text-transform: uppercase;
    letter-spacing: 3px;
    margin-left:auto ;
    margin-right: auto;
    font-size: small;
}


.header_icon:hover > p{
    display: inline !important;
    color: #e50914;
}

.header_icon > .MuiSvgIcon-root{
    margin-left: auto;
    margin-right: auto;
}

.header > img{
    object-fit: contain;
    height: 80px;
}

.header_icon_active > p{
   display: inline !important;
}





.header_icon_active {
  color: #e50914;
}

@media (max-width: 600px) {
  .header {
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  

  } 
  .header_icon > .MuiSvgIcon-root{
      margin-bottom: 10px;
      font-size: 16px;
      display: flex;
}
   
    
  }

  



