.results > div{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
 
}


.results .flip-move {
  display: flex;
  flex-wrap: wrap;
}

.results .flip-move > div {
  margin: 10px;
}


@media (min-width: 768px) {
  .results .flip-move > div {
    width: calc(33.3333% - 20px); 
   
  }
}


@media (max-width: 767px) {
  .results .flip-move > div {
    width: calc(50% - 20px); /* Adjust the width based on your layout needs */
    margin: 10px;
  }
}